var Cube = {
    urls:{},
    init_list: ["urls"],
    init: function(args) {
        if (!args || typeof args === 'undefined') return;
        for (var key of this.init_list) {
            if (typeof args[key] !== 'undefined') {
                var d = args[key];
                var t = Cube.getType(d);

                var t2 = (typeof(this[key]) !== 'undefined') ? Cube.getType(this[key]) : "string";
                if ((t === 'array' && t2 === 'array') || (t === 'object' && t2 === 'object')) {
                    for (var k in d) {
                        var v = d[k];
                        this[key][k] = v;
                    }
                } else {
                    this[key] = d;
                }
            }
        }
    },
    
};
Cube.getType = function(obj) {
    var t = 'null';
    if (typeof obj === 'object') {
        if (obj === null) {
            t = 'null';
        } else if (obj.constructor === FormData) {
            t = 'formdata';
        } else if (obj.constructor === Array) {
            t = 'array';
        } else if (obj.constructor === Object) {
            t = 'object';
        } else if (obj.constructor === Number) {
            t = 'number';
        } else {
            t = 'object';
        }
    } else {
        t = typeof obj;
    }
    return t;
};
