Cube.url.toFormData = function (obj){
    var d = new FormData();
    if(typeof obj === 'undefined') return d;
    else if(Cube.getType(obj)==='formdata'){
        d = obj;
    }
	else if(Cube.getType(obj)==='object'){
        for(var k in obj){
            d.append(k,obj[k]);
        }
    }
    return d;
};
Cube.url.encode = function(str){
    return encodeURIComponent(str);
};
Cube.url.decode = function(str){
    return decodeURIComponent(str);
};


Cube.url.init = function(){
    var a = arguments;
    if(a.length>0){
        var d = a[0];
        if(Cube.getType(d)==='object'){
            for(var k in d){
                if(!this.list[k]){
                    this[k] = d[k];
                }
            }
        }
    }
};




