import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Loadable from 'react-loadable';
import React, { Component } from 'react';



import LS from "./lib/LS";

import Messenger from './core/Messenger';

const Loading = () => <div className="loadinng">Loading...</div>;

const Home = Loadable({
    loader: () => import('./Home'),
    loading: Loading,
});


const Login = Loadable({
    loader: () => import('./Login'),
    loading: Loading,
});
  
const Register = Loadable({
    loader: () => import('./Register'),
    loading: Loading,
});


const Logout = Loadable({
    loader: () => import('./Logout'),
    loading: Loading,
});


const ChatBox = Loadable({
    loader: () => import('./ChatBox'),
    loading: Loading,
});

const Profile = Loadable({
    loader: () => import('./Profile'),
    loading: Loading,
});

class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            theme:'dark'
        };
    }
    render() {
        let {theme} = this.state;
        let theme2 = LS.get('app_theme');
        if(theme2) theme = theme2;
        let cls = 'app ' + theme;
        return (
            <div className={cls}>
                <Router>
                    <Switch>
                        <Route exact path="/" component={Home}/>
                        <Route path="/login" component={Login}/>
                        <Route path="/register" component={Register}/>
                        <Route path="/logout" component={Logout}/>
                        <Route path="/messages" component={ChatBox}/>
                        <Route path="/profile" component={Profile}/>
                    </Switch>
                </Router>
            </div>
        )
    }

    componentWillMount(){
        let data = {
            socket_server:'https://messenger-react.herokuapp.com/',
            api_server:"http://chinhlatoi.com/api/"
        };
        let access_token = LS.get('access_token');
        if(!access_token || access_token === null || access_token === undefined){
            LS.remove('access_token');
        }else{
            data.access_token = access_token;
        }
        Messenger.init(data);
        Messenger.socket();
        console.warn = function(params) {
            // del lam gi
        };

    }

    componentDidMount(){
        this.IOEventListener();
    }
    IOEventListener = () => {
        let socket = Messenger.socket();
        socket.on('change-theme', theme => {
            if(['dark', 'light', 'gradient'].indexOf(theme) !== -1){
                LS.set('app_theme', theme);
                this.setState({theme});
            }
        });
    }

    redirect = (path) => {
        if(path){
            if(path[0] !== '/') path = '/'+path;
            this.props.history.push(path);
        }
    }

}
export default App;


