
import socketIOClient from "socket.io-client";

import Cube from "../lib/Cube";
import LS from "../lib/LS";

var Messenger = {
    data:{},
    socket_server:null,
    api_server:null,
    urls:{},
    access_token:null,

    _socket:null,

    user:{},
    messages:[],

    init_list: ["access_token", "socket_server", "api_server", "user", "messages"],
    init: function(args) {
        if (!args || typeof args === 'undefined') return;
        for (var key of this.init_list) {
            if (typeof args[key] !== 'undefined') {
                var d = args[key];
                var t = Cube.getType(d);
                var t2 = (typeof(this[key]) !== 'undefined') ? Cube.getType(this[key]) : "string";
                if ((t === 'array' && t2 === 'array') || (t === 'object' && t2 === 'object')) {
                    for (var k in d) {
                        var v = d[k];
                        this[key][k] = v;
                    }
                } else {
                    this[key] = d;
                }
            }
        }
    },
    set: function(args) {
        if (!args || typeof args === 'undefined') return;
        for (var key of this.init_list) {
            if (typeof args[key] !== 'undefined') {
                var d = args[key];
                var t = Cube.getType(d);
                var t2 = (typeof(this[key]) !== 'undefined') ? Cube.getType(this[key]) : "string";
                if ((t === 'array' && t2 === 'array') || (t === 'object' && t2 === 'object')) {
                    for (var k in d) {
                        var v = d[k];
                        this[key][k] = v;
                    }
                } else {
                    this[key] = d;
                }
            }
        }
    },
    
    socket:function () {
        if(!this._socket || this._socket === null){
            const _socket = socketIOClient(this.socket_server);
            this._socket = _socket;
        }
        return this._socket;
    },

     // sending sockets
    
    // send data to api server

    request: function (path, method, data, callback, error) {
        Cube.url.api(this.api_server+path,method, data, (res, status) => {
            let data = {};
            try{
                data = JSON.parse(res);
            }catch(e){
                console.log(e.message);
            }
            if(data){
                callback(data);
            }else{
                error(res, status);
            }
        }, error);
    },

    checkLogin : function(){
        if(this.access_token && this.user && this.user.access_token && this.access_token === this.user.access_token){
            return true;
        }
        return false;
    },

    timeData:{

    },

    addTimeoutID:function(name, value){
        this.timeData[name] = value;
    },

    getTimeoutID(name){
        if(typeof this.timeData[name] != 'undefined' && this.timeData[name]){
            return this.timeData[name];
        }
        return null;
    },

    removeTimeoutID(name){
        if(typeof this.timeData[name] != 'undefined' && this.timeData[name]){
            this.timeData[name] = null;
            try{
                delete this.timeData[name];
            }catch(e){
                console.log(e);
            }
        }
    }
};

export default Messenger;


